import Vue from "vue";
import VueRouter, { RouteConfig } from "vue-router";

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: "/",
    name: "Home",
    redirect: "/money",
  },
  {
    path: "/money",
    name: "Money",
    component: () => import("../views/Money.vue"),
  },
  {
    path: "/statements",
    name: "Statements",
    component: () => import("../views/Statements.vue"),
    children: [
      {
        path: "today",
        component: () => import("@/components/Statements/TodayFlow.vue"),
      },
      {
        path: "this_week",
        component: () => import("@/components/Statements/ThisWeekFlow.vue"),
      },
      {
        path: "this_month",
        component: () => import("@/components/Statements/ThisMonthFlow.vue"),
      },
      {
        path: "this_year",
        component: () => import("@/components/Statements/ThisYearFlow.vue"),
      },
    ],
  },
  {
    path: "/statistics",
    name: "Statistics",
    component: () => import("../views/Statistics.vue"),
  },
  {
    path: "/my",
    name: "My",
    component: () => import("../views/My.vue"),
    children: [
      {
        path: "outcome_setting",
        component: () => import("@/components/My/OutcomeSetting.vue"),
      },
      {
        path: "income_setting",
        component: () => import("@/components/My/IncomeSetting.vue"),
      },
      {
        path: "budget_setting",
        component: () => import("@/components/My/BudgetSetting.vue"),
      },
      {
        path: "currency_setting",
        component: () => import("@/components/My/CurrencySetting.vue"),
      },
    ],
  },
  {
    path: "/change_psw",
    component: () => import("@/components/My/ChangePsw.vue"),
  },
  {
    path: "*",
    name: "NotFound",
    component: () => import("../views/NotFound.vue"),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
