import axios from "axios";
import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userId: "",
    isLogined: false,
    costTags: [],
    // costTags: ["食品酒水", "衣服饰品", "居家物业", "行车交通", "交流通讯", "休闲娱乐", "学习进修", "人情往来", "医疗保健", "其他杂项"],
    incomeTags: [],
    recordList: [] as RecordItem[],
    localStorageKeyName: "recordList",
    Date: new Date(),
    flowTitle: "",
    dayItems: [],
    thisWeekItems: [],
    thisMonthItems: [],
    thisYearItems: [],
  },
  getters: {
    currentMonth(state) {
      return state.Date.getMonth() + 1;
    },
    currentYear(state) {
      return state.Date.getFullYear();
    },
    currentDay(state) {
      return state.Date.getDate();
    },
  },
  mutations: {
    updateDate(state) {
      state.Date = new Date();
    },
    selectTag(state) {},
    createRecord(state, record) {
      // const record
    },
    updateRecordList(state, data) {
      state.recordList = data;
    },
    updateOutTag(state, tags) {
      state.costTags = tags;
    },
    updateInTag(state, tags) {
      state.incomeTags = tags;
    },
    updateUserId(state, userId) {
      state.userId = userId;
    },
    changeLoginState(state) {
      state.isLogined = !state.isLogined;
    },
  },
  actions: {
    fetch(context) {
      let fetchResult = window.localStorage.getItem(this.state.localStorageKeyName);
      if (fetchResult === "undefined") {
        fetchResult = "[]";
      }
      context.commit("updateRecordList", JSON.parse(fetchResult || "[]") as RecordItem[]);
    },
    saveRecordList(context, data: RecordItem[]) {
      window.localStorage.setItem("recordList", JSON.stringify(data));
      context.dispatch("fetch");
    },
    getSettings(context, userId) {
      axios
        .post("http://localhost:3000/api/account/fetch/settings", { userId })
        .then((result) => {
          context.commit("updateOutTag", result.data.data.expenditureTag);
          context.commit("updateInTag", result.data.data.incomeTag);
        })
        .catch((err) => {
          console.warn(err);
        });
    },
  },
  modules: {},
});
